// export function singleton(ctor: new(...args) => {}) { // TODO: 
export function singleton<T>(ctor: new(...args: any[]) => T) { // TODO: 
    let instance: {};
    Object.defineProperty(ctor, "instance", {
        get() {
            if(!instance) {
                instance = new ctor();
            }

            return instance;
        }
    });
}
